@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
* {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0%;
  padding: 0%;
  max-width: 100vw;
  max-height: 100vh;
}

.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

.app-content {
  width: 50%;
  display: flex;
}

.left {
  width: 25%;
  padding: 16px;
  background-color: #313131;
}

.centre {
  display: flex;
  flex-direction: column;
  width: 60%;
  background-color: #3b3b3b;
}

.task-lists {
  width: 100%;
}

.task-list-item-container {
  display: flex;
  align-items: center;
  font-size: 18px;
  background-color: #525252;
  border-radius: 5px;
}

.task-list-item-container input {
  display: flex;
  align-items: center;
  padding: 10px;
  width: 100%;
  font-size: 16px;
  color: #b4b4b4;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.task-list-item-container input:hover {
  transition: 250ms;
  color: #ffffff;
}

.task-list-item-container input::-webkit-input-placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input:-ms-input-placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input::placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input:focus {
  outline: none;
}

.task-list-items {
    margin: 10px 0px;
}
.task-list-item {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    padding: 5px 0px;
    cursor: pointer;
}

.task-list-item:hover {
    transition: 250ms;
    color: #FFFFFF;
}

.task-list-size {
    flex-grow: 1;
    display: flex;
    justify-content: right;
}
.task-list {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 12px;
}

.task-list-name {
  border: none;
  background-color: transparent;
  color: #ffffff;
  font-size: 24px;
}

.task-list-name:focus {
  border: none;
  outline: none;
}

.task-list-details {
  margin: 5px 0px;
  font-size: 16px;
  color: #ffffffc0;
}

.task-list-list {
  flex-grow: 1;
  overflow: hidden;
  position: relative;
}

.task-list-name-details {
  margin-bottom: 10px;
}

.task-list-list-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
}

.tasklists-empty {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #686868;
}

.tasklists-empty .icon {
  font-size: 250px;
}

.tasklists-empty .text {
  font-size: 26px;
}

.task {
  min-height: 36px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 8px;
  margin-right: 5px;
  background-color: #2f2f2f;
}
.three-dot-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #ffffffb7;
  cursor: pointer;
}

.task-labels-container {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
}

.task-content-container {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
}

.task .task-content {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task-content .task-name {
  width: 95%;
  font-size: 16px;
  border: none;
  outline: none;
  background-color: transparent;
  color: #ffffff;
}

.task-content .task-completed {
  display: flex;
  justify-content: center;
  margin-right: 10px;
  font-size: 24px;
  width: 5%;
  cursor: pointer;
}

.task-completed .completed-false {
  color: #8d8d8d;
}

.task-completed .completed-true {
  color: #00c853;
}

.task-edit {
  display: flex;
  align-items: center;
  font-size: 18px;
  color: rgb(139, 139, 139);
  cursor: pointer;
}

.task-panel-component {
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.task-panel-item-task-label-form {
  margin-bottom: 10px;
  font-size: 16px;
  color: #ffffffd0;
}

.task-panel-item-delete-button {
  min-height: 50px;
  display: flex;
}
.task-label-form-item-center {
  margin: 16px 0px;
}
.task-label-container {
  display: flex;
  flex-direction: row;
  cursor: pointer;
}

.task-label-radio {
  display: flex;
  align-items: center;
  margin-right: 16px;
  font-size: 16px;
}
.task-label {
  width: 90px;
  text-align: center;
  font-size: 14px;
  letter-spacing: 0.5px;
  color: #ffffff;
  padding: 3px 8px;
  border-radius: 100px;
  white-space: nowrap;
}

.delete-task {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  background-color: #d32f2f;
  color: #ffffff;
  padding: 5px 8px;
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.delete-task-icon {
  display: flex;
  align-items: center;
  margin-left: 16px;
}
.task-form {
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 5px;
  margin: 5px 0px;
  padding: 0px 10px;
  background-color: #525252;
}

.task-form-container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.input-container {
  display: flex;
  flex-direction: row;
  border-radius: 8px;
  width: 100%;
}

.input-container input {
  width: 100%;
  padding: 16px 8px;
  font-size: 16px;
  border: none;
  background-color: transparent;
  color: #ffffff;
}

.input-container input::-webkit-input-placeholder {
  color: #ffffff;
  opacity: 0.6;
}

.input-container input:-ms-input-placeholder {
  color: #ffffff;
  opacity: 0.6;
}

.input-container input::placeholder {
  color: #ffffff;
  opacity: 0.6;
}

.input-container input:focus {
  outline: none;
}

